import { toast } from "react-toastify";
import styled from "styled-components";
import React, { useEffect } from "react";
import { useHistory } from "react-router";
// import AdminNav from "../components/AdminNav";
import { useDispatch, useSelector } from "react-redux";
import { OrdersList } from "../../actions/orderAction";

import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

import { ReportsList } from "../../actions/ReportAction";
import AdminNavs from "../../components/Admin/AdminNavs";
import { BsCheck2, BsCheckCircleFill } from "react-icons/bs";
import { MdOutlinePending, MdOutlineReport, MdOutlineSell } from "react-icons/md";

const Dashboard = ({ history }) => {
  const dispatch = useDispatch();
  const { loading, error, orders } = useSelector((state) => state.orderList);
  const {
    reports,
    loading: reportLoading,
    error: reportError,
  } = useSelector((state) => state.reportList);

  const user = useSelector((state) => state.user);

  const pending =
    !loading &&
    !error &&
    orders &&
    orders.filter((x) => x.isDelivered === false);
  const delivered =
    !loading &&
    !error &&
    orders &&
    orders.filter((x) => x.isDelivered === true);
  const reportsTotal =
    !reportLoading && !reportError && reports && reports.length;
  const totalSell =
    !loading &&
    !error &&
    orders &&
    delivered &&
    delivered.map((x) => x.totalPrice).reduce((prev, curr) => prev + curr, 0);

  if (error) {
    toast.error(`${error}`);
  }
  if (reportError) {
    toast.error(`${reportError}`);
  }

  useEffect(() => {
    if (user && user.isAdmin) {
      dispatch(OrdersList());
      dispatch(ReportsList());
    } else {
      history.push("/login");
    }
  }, [dispatch, user]);

  return (
    <>
      <AdminNavs />

      <section class="text-gray-600 body-font">
        <div class="container bg-gray-50 px-5 py-24 mx-auto">
          <div class="flex flex-wrap -m-4 text-center">
            <div class="p-4 md:w-1/4 sm:w-1/2 w-full">
              <div class="border-2 border-gray-200 px-4 py-6 rounded-lg">
                {/* text-indigo-500 w-12 h-12 mb-3 inline-block */}
                <BsCheck2 class="text-indigo-500 w-12 h-12 mb-3 inline-block " />
                <h2 class="title-font font-medium text-3xl text-gray-900">
                  {delivered.length}
                </h2>
                <p class="leading-relaxed">Delivered Orders</p>
              </div>
            </div>
            <div class="p-4 md:w-1/4 sm:w-1/2 w-full">
              <div class="border-2 border-gray-200 px-4 py-6 rounded-lg">
                <MdOutlinePending class="text-indigo-500 w-12 h-12 mb-3 inline-block " />
                <h2 class="title-font font-medium text-3xl text-gray-900">
                  {pending.length}
                </h2>
                <p class="leading-relaxed">Pending Orders</p>
              </div>
            </div>
            <div class="p-4 md:w-1/4 sm:w-1/2 w-full">
              <div class="border-2 border-gray-200 px-4 py-6 rounded-lg">
                <MdOutlineSell class="text-indigo-500 w-12 h-12 mb-3 inline-block " />
                <h2 class="title-font font-medium text-3xl text-gray-900">
                  {totalSell} /-
                </h2>
                <p class="leading-relaxed">Total Sell</p>
              </div>
            </div>
            <div class="p-4 md:w-1/4 sm:w-1/2 w-full">
              <div class="border-2 border-gray-200 px-4 py-6 rounded-lg">
                <MdOutlineReport class="text-indigo-500 w-12 h-12 mb-3 inline-block " />
                <h2 class="title-font font-medium text-3xl text-gray-900">
                  {reportsTotal} 
                </h2>
                <p class="leading-relaxed">Reports</p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Dashboard;
